import React, {useEffect} from "react";
import Registration from '../components/Account/Registration';
import {useSelector} from "react-redux";
import axios from "axios";
import allActions from "../redux/actions";
const {pageOptionsConfiguration} = require('../components/axiosConfigs/pageOptionsConfiguration')

export default function PageSetup(){

    const uId = useSelector(state => state.userState.uId);

    useEffect(()=> {
        console.log(pageOptionsConfiguration(uId))
        // make the API call
        axios((pageOptionsConfiguration)(uId))
            .then((response) => {
                console.log('eblan')
            })
            .catch((error) => {
                console.log(error)
            });
    })
    return (
        <p> test axios configs</p>
    )
}